import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { CharacterService } from '../character.service';
import { NinjutsuService } from '../ninjutsu.service';

import { AlertController, ToastController } from '@ionic/angular';

@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {

  ten: number;
  chi: number;
  jin: number;

  available_ten: number;
  available_chi: number;
  available_jin: number;

  ninjutsus: any;

  constructor( 
    private router: Router,
    public alertController: AlertController ,
    public character: CharacterService,
    public toastController: ToastController,
    public ninjutsu: NinjutsuService
  ) {
    this.updateMudra();
    this.ninjutsu.getNinjutsu().then( res => {
      this.ninjutsus = res;
    });
  }

  updateMudra(){
    this.ten = 0;
    this.chi = 0;
    this.jin = 0;


    if( !isNaN(this.character.level)){
      if( this.character.level >= 3 ){
        this.ten = Math.floor( this.character.level / 2 );
      }
      if( this.character.level >= 9){
        this.chi = Math.floor( this.character.level / 2 );
      }
      if( this.character.level >= 13){
        this.jin = Math.floor( this.character.level / 2 );
      }
    }

    this.available_chi = this.chi;
    this.available_jin = this.jin;
    this.available_ten = this.ten;

  }


  loadNinjutsu( id: number ){
    this.router.navigate(['/ninjutsu/' + id ])
  }

  useMudra( type: string ){
    if( type == 'ten'  && this.available_ten > 0 ){
      this.available_ten--;
    }
    if( type == 'chi'  && this.available_chi > 0 ){
      this.available_chi--;
    }
    if( type == 'jin'  && this.available_jin > 0 ){
      this.available_jin--;
    }
  }

  longRest(){
    this.updateMudra();
    this.finishedLongRest();
  }




  editLevel(){
    this.setCharacterLevel();
  }


  // controllers
  async finishedLongRest(){
    const toast = await this.toastController.create({
      message: "You've taken a long rest!",
      duration: 2000,
      color: "dark",
      keyboardClose: true
    });
    toast.present();
  }
  
  async setCharacterLevel(){
    const alert = await this.alertController.create({
      header: "Set Character Level",
      inputs: [{
        name: "level",
        type: "number",
        value: this.character.level,
        min: 1,
        max: 20
      }],
      buttons: ['Cancel', {
        text: "Save",
        cssClass: 'tertiary',
        handler: ( data ) => {
          var characterLevel = parseInt( data.level );
          if( characterLevel >= 1 && characterLevel <= 20 ){
            console.log("Valid!");
            this.character.level = characterLevel;
            console.log("Character Level: ", this.character.level )
            this.character.setLevel( characterLevel );
            this.updateMudra();
          } else {
            console.log("Invalid")
          }
        }
      }]
    });
    await alert.present();
  }

}
